.container {
  display: flex;
  flex-direction: column;
  justify-content: start;
  height: 100%;
  height: 70dvh;
  max-height: 625px;
  width: 100%;
  max-width: 300px;
  align-items: start;
  padding-left: 1rem;
  background-color: #1B1F22;
  box-sizing: border-box;
  box-shadow: 0 0 15px 5px  rgba(0,0,0,0.35);
}

.betBtn {
  margin-top: 3dvh;
  width: 100%;
}

.balance {
  margin-top: 3dvh;
}

.inputBlock {
  padding-bottom: 30px;
  border-bottom: 1px solid #E7EBEE ;
  width: 100%;
}

.mineBlock {
  padding-bottom: 30px;
  border-bottom: 1px solid #E7EBEE ;
  width: 100%;
}

@media (max-width: 915px) {
  .container {
    box-sizing:border-box;
    display: flex;
    flex-direction: column;
    padding-right: 20px;
     width: 90dvw;
  max-width: 1200px;
  height: 100%;
  max-height: 284px;
  }
  
  .inputBlock {
  padding-bottom: 5px;
  border-bottom: none;
  width: 98%;
}

.mineBlock {
  padding-bottom: 5px;
  border-bottom: none;
  width: 99%;
}

.betBtn {
  margin-top: 1dvh;
  width: 100%;
  min-height: 15%;
}

}
