.container {
  position: absolute;
  display: none;
  justify-content: center;
  align-items: center;
  top: 18px;
  right: 17px;
  bottom: 18px;
  left: 17px;
  background-color: #1c1f2277;
  z-index: 99;
}


.content {
  display: flex;
  font-size: clamp(0.3rem, 4dvw, 1.3rem);
  min-width: 20%;
  max-width: 60%;
  height: 20%;
  border-radius: 20px;
  border: 5px solid #20B66B;
  color: #20B66B;;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.x {
  margin-bottom: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid #E7EBEE ;
}

.active {
  display: flex;
}

.winBlock {
  display: flex;
  align-items: center;
  justify-content: center;

}

.currency {
  padding-left: 10px;
  width: clamp(0.3rem, 4dvw, 1.7rem) ;
}