.text {
  
 width: 15dvw;
 font-size: clamp(0.7rem, 4dvw, 1rem);
 color: #dccece;
 width: 100%;
}

.currency {
  margin-left: 0.4rem;
  width: 30px;
  height: 30px;
}

.btc {
  padding-left: 1rem;
  
}

.currencyBlock {
display: flex;
flex-direction: row;
align-items: center;
justify-content: start;
}


@media (max-width: 915px) {
  .container {
   display: flex; 
  justify-content: space-between; 
  align-items: center;
  flex-direction: row;
  width: 100%;
  }

  .text {
    font-size: clamp(0.3rem, 2dvw, 1rem);
  }
  
}

@media (max-width: 915px) {
  .button {
    font-size: clamp(0.5rem, 2dvw, 1.2rem);
  }
  
}