.input{
  background-color: var(--grey-700);
  width: 100%;
  height: 100%;
 font-size: clamp(0.5rem, 4dvw, 1.3rem);
 color: #E7EBEE;
 
  display: flex;
    align-items: center;
    background-color: var(--grey-700);
}

.intputLabel {
font-size: clamp(0.5rem, 4dvw, 1.3rem);;
color: #E7EBEE;
}

.inputBlock{
  position: relative;
}


.btc {
  position: absolute;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: end;
  padding-right: 20px;
  pointer-events: none;
}



@media (max-width: 915px) {
  .input {
    font-size: clamp(0.3rem, 2dvw, 1rem);
  }

  .intputLabel {
    font-size: clamp(0.3rem, 2dvw, 1rem);
  }
  
}