.select {
  background-color: var(--grey-700);
  width: 100%;
  font-size: clamp(0.5rem, 4dvw, 1.3rem);;
  color: #E7EBEE;
}

.selectLabel {
font-size: clamp(0.5rem, 4dvw, 1.3rem);
color: #E7EBEE;
}

@media (max-width: 915px) {
  .select {
    font-size: clamp(0.3rem, 2dvw, 1rem);
    
  }

  .container {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: center;
    margin-top: 10px;
  }

  .selectLabel {
    padding: 0;
    margin-bottom: 5px;
    font-size: clamp(0.3rem, 2dvw, 1rem);
  }
  
}