.App {
  display: flex;
  background-color: #121010;
  width: 90dvw;
  max-width: 1200px;
  height: 70dvh;
  max-height: 625px;
  margin-top: 20px;
  font-size: clamp(1.5rem, 4vw, 2.5rem);
}

@media (max-width: 915px) {
  .App {
    margin-top: 50px;
    flex-direction: column-reverse;
    height: 100%;
    max-height: 790px;
  }
  
}