.fieldWrapper  {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #1B1F22;
  box-shadow: 10px 0 15px -5px  rgba(0,0,0,0.35);
  width: 90dvw;
  
}

.field{
 padding: 1rem;
 position: relative;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 0.5rem;
  width: 90dvw;
  max-width: 610px;
  height: 70dvh;
  max-height: 610px;
}

.text {
position: absolute;
 /* display: none; */
 text-align: center;
 font-size: 4rem;
 left: 0;
 right: 0;
 top: 0;
 bottom: 0;
 z-index: 99;
 background-color: #1b1f228b;
}


.win {
  display: flex;
  font-size: 3rem;
  background-color: #108b4b;
  
}

@media (max-width: 915px) {
  .field {
    height: 80dvh;
    max-height: 530px;
  }
  
}