.mine {
  background-color: var(--grey-400);
  background: linear-gradient(#24292E, #1C1F22);
  box-shadow: 
    0 0 0 2px #34383D,
    inset 0 0 0 4px rgba(255, 255, 255, 0.04); 
  width: 100%;
  /* max-width: 100px; */
  height: 100%;
  /* max-height: 100px; */
  border-radius: 1rem;
  transition: all 0.2s cubic-bezier(0.075, 0.82, 0.165, 1);
  transition: background-color 3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.mine:hover {
  cursor: pointer;
  box-shadow: 0 0 0 5px rgba(47,218,133,0.15);
  transform: scale(1.03); 
 
}

.mine:focus {
  cursor: pointer;
  box-shadow: 0 0 0 5px rgba(47,218,133,0.40);
  transform: scale(1.03); 
 
}


.mineBomb {
  background-image: url('/public//mines.png');
  background-color: var(--red-400);
  background-repeat: no-repeat;
  
  background-position: center;
  
  background-size: cover;
}

.diamond {
  background-image: url('/public//diamond.png');
  background-color: var(--yellow-400);
  background-repeat: no-repeat;
  
  background-position: center;
  
  background-size: cover;
}

